import React, { useRef, useEffect } from "react";
import retinaCanvasScaling from "./retinaCanvasScaling";
import { setTimeout } from "timers";

// import rough from "roughjs";
const rough = require("roughjs/bundled/rough.cjs.js");
// import rough from "roughjs/bundled/rough.esm.js";
// const rough = require("../node_modules/roughjs/dist/rough.umd");

export type UnitInterval = {
  A: number;
  from: number;
  B: number;
  to: number;
  color: string;
  title: string;
};

export type LifeProps = {
  row: number;
  column: number;
  animatedUnit: number;
  widthPx: number;
  heightPx: number;
  lineWidth: number;
  intervals?: Array<UnitInterval>;
  style?: React.CSSProperties;
};

export type Point = {
  x: number;
  y: number;
};
export type Size = {
  dx: number;
  dy: number;
};
export type Range = {
  from: number;
  to: number;
};

export default function Life({
  row,
  column,
  animatedUnit,
  widthPx: width,
  heightPx: height,
  lineWidth,
  intervals,
  style,
}: LifeProps) {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const ctxRef = React.useRef<CanvasRenderingContext2D | null>(null);

  useEffect(() => {
    let requestId: number;
    let i = 0;
    const unitSize: Size = { dx: width / row, dy: height / column };

    const strokeUnit = (ctx: CanvasRenderingContext2D, center: Point, size: Size, color: string) => {
      ctx.strokeStyle = color;
      const cx = center.x;
      const cy = center.y;
      const dx = size.dx;
      const dy = size.dy;
      //   ctx.strokeRect(cx - dx / 2 + 1, cy - dy / 2 + 1, dx + 0.5, dy + 0.5);
      ctx.strokeRect(cx - dx / 2, cy - dy / 2, dx, dy);
      //   const path = [
      //     [cx - dx / 2, cy],
      //     [cx, cy - dy / 2],
      //     [cx + dx / 2, cy],
      //     [cx, cy + dy / 2],
      //   ];
      //   ctx.beginPath();
      //   ctx.moveTo(path[0][0], path[0][1]);
      //   path.forEach((value) => {
      //     ctx.lineTo(value[0], value[1]);
      //   });
      //   ctx.closePath();
      //   ctx.stroke();
    };

    const fillUnit2 = (rc: any, center: Point, size: Size, color: string, range: Range) => {
      const cx = center.x;
      const cy = center.y;
      const dx = size.dx;
      const dy = size.dy;
      const from = range.from;
      const to = range.to;
      // ctx.fillRect(cx - dx / 2 + from * dx, cy - dy / 2, dx * (to - from), dy);
      rc.rectangle(cx - dx / 2 + from * dx, cy - dy / 2, dx * (to - from), dy, {
        fill: color,
        fillStyle: "cross-hatch",
      });
    };

    const fillUnit = (ctx: CanvasRenderingContext2D, center: Point, size: Size, color: string, range: Range) => {
      ctx.fillStyle = color;
      const cx = center.x;
      const cy = center.y;
      const dx = size.dx;
      const dy = size.dy;
      const from = range.from;
      const to = range.to;
      ctx.fillRect(cx - dx / 2 + from * dx, cy - dy / 2, dx * (to - from), dy);

      //   const startX = cx - dx / 2 + from * dx;
      //   const endX = cx - dx / 2 + to * dx;
      //   const startYTop = from < 0.5 ? cy - from * dy : cy - dy / 2 + (from - 0.5) * dy;
      //   const startYBottom = from < 0.5 ? cy + from * dy : cy + dy / 2 - (from - 0.5) * dy;
      //   const endYTop = to < 0.5 ? cy - to * dy : cy - dy / 2 + (to - 0.5) * dy;
      //   const endYBottom = to < 0.5 ? cy + to * dy : cy + dy / 2 - (to - 0.5) * dy;

      //   const path = [];
      //   path.push([startX, startYTop]);
      //   if (from < 0.5 && to > 0.5) {
      //     path.push([cx, cy - dy / 2]);
      //   }
      //   path.push([endX, endYTop]);
      //   path.push([endX, endYBottom]);
      //   if (from < 0.5 && to > 0.5) {
      //     path.push([cx, cy + dy / 2]);
      //   }
      //   path.push([startX, startYBottom]);

      //   ctx.beginPath();
      //   ctx.moveTo(path[0][0], path[0][1]);
      //   path.forEach((value) => {
      //     ctx.lineTo(value[0], value[1]);
      //   });
      //   //   ctx.closePath();
      //   ctx.fill();
    };
    //
    const scaledSize = (size: Size, scalingX: number, scalingY: number) => {
      const newSize: Size = {
        dx: size.dx * scalingX,
        dy: size.dy * scalingY,
      };
      return newSize;
    };
    const animatedSize = (size: Size, i: number) => {
      const newSize: Size = {
        dx: Math.max(size.dx * 1.2 * Math.abs(Math.sin(i)), size.dx * 1),
        dy: Math.max(size.dy * 1.2 * Math.abs(Math.sin(i)), size.dy * 1),
      };
      return newSize;
    };
    const unitCenter = (unitNumber: number) => {
      const y = Math.floor(unitNumber / row);
      const x = unitNumber - y * row;
      const center: Point = { x: x * unitSize.dx + unitSize.dx / 2, y: y * unitSize.dy + unitSize.dy / 2 };
      return center;
    };
    const unitRange = (unit: number, interval: UnitInterval): Range => {
      const range: Range = {
        from: unit === interval.A ? interval.from : 0,
        to: unit === interval.B ? interval.to : 1,
      };
      return range;
    };
    //
    const roughConfig = {
      roughness: 5,
      bowing: 6,
      //   seed: Math.floor(Math.random() * 100000),
      seed: 2,
      stroke: 2,
    };

    if (canvasRef.current) {
      ctxRef.current = canvasRef.current.getContext("2d");
      const canvas = canvasRef.current;
      const ctx = ctxRef.current;
      const rc = rough.canvas(canvas, roughConfig);
      if (ctx) {
        retinaCanvasScaling(canvas, ctx, width, height);
        ctx.lineWidth = lineWidth;
        // ctx.shadowColor = "aquamarine";
        // ctx.shadowBlur = 5;

        // grid nodes
        const gridDrawables: Array<any> = [];
        for (let unit = 0; unit < row * column; ++unit) {
          const center = unitCenter(unit);
          const size = scaledSize(unitSize, 0.9, 0.9);
          //   const unitSizeA = animatedSize(size, i);
          gridDrawables.push(
            rc.generator.rectangle(center.x - size.dx / 2, center.y - size.dy / 2, size.dx, size.dy, {
              //   fill: "aquamarine",
              //   fillStyle: "cross-hatch",
            })
          );
        }

        // interval fill nodes
        const intervalsDrawables: Array<any> = [];
        if (intervals && intervals.length > 0) {
          intervals.forEach((interval, index) => {
            for (let unit = interval.A; unit <= interval.B; ++unit) {
              const center = unitCenter(unit);
              const range = unitRange(unit, interval);
              const size = scaledSize(unitSize, 0.9, 0.9);

              intervalsDrawables.push(
                rc.generator.rectangle(
                  center.x - size.dx / 2 + range.from * size.dx,
                  center.y - size.dy / 2,
                  size.dx * (range.to - range.from),
                  size.dy,
                  {
                    fill: interval.color,
                    fillStyle: "cross-hatch",
                  }
                )
              );
            }
          });
        }

        const render = () => {
          ctx.clearRect(0, 0, canvas.width, canvas.height);

          // draw all intervals ==============================
          //   if (intervals && intervals.length > 0) {
          //     intervals.forEach((interval, index) => {
          //       for (let unit = interval.A; unit <= interval.B; ++unit) {
          //         const center = unitCenter(unit);
          //         const range = unitRange(unit, interval);
          //         const size = scaledSize(unitSize, 0.9, 0.9);
          //         const unitSizeA = animatedSize(size, i);
          //         if (unit === animatedUnit) {
          //           fillUnit(ctx, center, unitSizeA, interval.color, range);
          //           //   fillUnit2(rc, center, unitSizeA, interval.color, range);
          //         } else {
          //           //   fillUnit(ctx, center, size, interval.color, range);
          //           //   fillUnit2(rc, center, size, interval.color, range);
          //         }
          //       }
          //     });
          //   }
          // draw all units ==============================
          //   for (let unit = 0; unit < row * column; ++unit) {
          //     const center = unitCenter(unit);
          //     const size = scaledSize(unitSize, 0.9, 0.9);
          //     const unitSizeA = animatedSize(size, i);
          //     if (unit === animatedUnit) {
          //       strokeUnit(ctx, center, unitSizeA, "hotpink");
          //     } else {
          //       strokeUnit(ctx, center, size, "hotpink");
          //     }
          //   }
          // draw rough ==============================
          //   rc.rectangle(10, 10, 20, 20, { fill: "hotpink", fillStyle: "cross-hatch" });
          //   rc.circle(50, 50, 80, { fill: "aquamarine", fillStyle: "zigzag" });

          //   grid.forEach((cell) => rc.draw(cell));
          intervalsDrawables.forEach((dr) => rc.draw(dr));
          gridDrawables.forEach((dr) => rc.draw(dr));

          const path =
            "M282 40.486C283 23 267 19 246 13S187 0 174 18C119 35 153 73 184 60 211 71 235 59 247 66 245.19 74.014 238.068 77.794 232.728 79 263.055 76.33 280 63 282 40Z";
          if (column === 9) {
            rc.path(path, { fillStyle: "solid", fill: "purple" });
          }
          const path2 =
            "M-26 189.352c0 20.24 21.492 36.648 48 36.648s48-16.408 48-36.648c-0-20.388-21.788-36.852-48.484-36.644 1.564-8.06 11.06-13.1 18.18-14.708-40.436 3.56-65.696 25.956-65.696 51.352z";
          if (column === 9) {
            rc.path(path2, { fillStyle: "solid", fill: "purple" });
          }

          //   rc.draw(rect);
          i += 0.05;
          //   requestId = requestAnimationFrame(render);
        };
        // const timeout = setTimeout(() => {
        //   render();
        // }, 1000);
        render();
        return () => {
          cancelAnimationFrame(requestId);
        };
      }
    }
  }, [animatedUnit, row, column, width, height, lineWidth, intervals]);

  return <canvas style={style} ref={canvasRef} />;
}
